.home2-page .slider-container{
    margin:5% 0;
    padding: 0 10%;
    .rec-dot{
        display: none;
    }
    .slider-item{
        display: flex;
        align-items: center;
        justify-content: space-between;
        width:90%;
        margin:10px auto;
    }
    .img{
        content: url('../../../../assets/Home2/profile-img.png');
        width: 35%;
        border-radius: 10px;
    }
    .info{
        width: 50%;
    }
    h1{
        font-size: 35px;
    }
    .info span{
        color:#d4e1ff;
        font-size: 65px;
    }
    p{
        font-size: 18px;
        margin:15px 0 25px;
    }
    .name{
        display: flex;
        color:#427CFF;
        align-items: flex-end;
        h6{
            font-size: 18px;
            margin-right: 5px;
        }
        small{
            font-size: 16px;
        }
    }
    .rec-arrow,
    .rec-arrow-left,
    .rec-arrow-right{
        width: 60px !important;
        height: 60px !important;
        min-width: 60px !important;
        max-width: 60px !important;
        min-height: 60px !important;
        max-width: 60px !important;
        border-radius: 5px;
        background-color: #fff !important;
        font-size: 35px;
        color:#427CFF !important;
        border:1px solid #427CFF !important;
        box-shadow: none !important;
        &:enabled:hover, &:enabled:focus, &:enabled:active{
            background-color: #427CFF !important;
            border:1px solid #427CFF !important;
            color:#fff !important;
        }
        &:disabled:hover, &:disabled:focus, &:disabled:active, &:disabled{
            color:#999 !important;
            border:1px solid #999 !important;
        }
    }
}

@media(max-width:1401px){
    .home2-page .slider-container{
        padding: 0 10%;
        .slider-item{
            width:90%;
            margin:10px auto;
        }
        .img{
            width: 35%;
            border-radius: 10px;
        }
        .info{
            width: 55%;
        }
        h1{
            font-size: 30px;
        }
        .info span{
            font-size: 45px;
        }
        p{
            font-size: 15px;
            margin:10px 0 20px;
        }
        .name{
            h6{
                font-size: 15px;
            }
            small{
                font-size: 13px;
            }
        }
        .rec-arrow,
        .rec-arrow-left,
        .rec-arrow-right{
            width: 45px !important;
            height: 45px !important;
            min-width: 45px !important;
            max-width: 45px !important;
            min-height: 45px !important;
            max-width: 45px !important;
            font-size: 25px;
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }
}


@media(max-width:767px){
    .home2-page .slider-container{
        margin:40px 0;
        padding: 0 2.5%;
        .slider-item{
            flex-direction: column;
            width:100%;
            margin:10px auto;
        }
        .img{
            width: 200px;
            max-width: 100%;
            margin:20px auto;
            border-radius: 7.5px;
        }
        .info{
            width: 100%;
            text-align: center;
        }
        h1{
            font-size: 20px;
        }
        .info span{
            font-size: 40px;
        }
        p{
            font-size: 14px;
        }
        .name{
            justify-content: center;
            h6{
                font-size: 14px;
            }
            small{
                font-size: 12px;
            }
        }
        .rec-arrow,
        .rec-arrow-left,
        .rec-arrow-right{
            width: 30px !important;
            height: 30px !important;
            min-width: 30px !important;
            max-width: 30px !important;
            min-height: 30px !important;
            max-width: 30px !important;
            border-radius: 3px;
            font-size: 20px;
        }
    }
}

