.home2-page .inner-last-row{
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-image: url('../../../../assets/Home2/last-row.png');
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    padding:5% 12.5%;
    a{
        text-decoration: none;
    }
    .block{
        color:white;
        width: 50%;
        h1{
            font-size: 45px;
            margin-bottom: 20px;
        }
        p{
            font-size: 18px;
        }
    }
    .card{
        background-color: #fff;
        padding:30px 50px;
        width: 37.5%;
        border-radius: 10px;
        text-align: center;
    }

    .inline{
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    .logo{
        content: url('../../../../assets/Navbar/blue-logo.png');
        width: 40px;
    }
    .inline span{
        color:#427CFF;
        font-family:cursive;
        font-style: italic;
        font-size: 20px;
    }
    h6{
        font-size: 22.5px;
        text-align: center;
        margin:15px 0;
    }
    .card p{
        text-align: center;
        margin-bottom: 30px;
        font-size: 18px;
    }
    .link{
        display: flex;
        justify-content: center;
    }
}

@media(max-width:1401px){
    .home2-page .inner-last-row{
        padding:5% 12.5%;
        .block{
            width: 45%;
            h1{
                font-size: 35px;
                margin-bottom: 17.5px;
            }
            p{
                font-size: 15px;
            }
        }
        .card{
            padding:20px 35px;
            width: 45%;
        }
    
        .logo{
            width: 35px;
        }
        .inline span{
            font-size: 17.5px;
        }
        h6{
            font-size: 17.5px;
            margin:15px 0;
        }
        .card p{
            margin-bottom: 30px;
            font-size: 15px;
        }
    }
}

@media(max-width:991px){
    .home2-page .inner-last-row{
        padding:5% 7.5%;
    }
}

@media(max-width:767px){
    .home2-page .inner-last-row{
        padding:35px 7.5%;
        display: block;
        .block{
            width: 100%;
            text-align: center;
            h1{
                font-size: 22.5px;
                margin-bottom: 15px;
            }
            p{
                font-size: 15px;
            }
        }
        .card{
            padding:20px 30px;
            width: 350px;
            max-width: 100%;
            margin:35px auto 10px;
        }
        h6{
            font-size: 16px;
            margin:10px 0;
        }
        .card p{
            margin-bottom: 25px;
            font-size: 15px;
        }
    }
    
}

.home2-page .inner-last-row{
    .card{
        transition: 0.35s ease-in-out;
        -webkit-transform: perspective(1px) translateZ(0);
        transform: perspective(1px) translateZ(0);
        box-shadow: 0 0 1px rgba(0, 0, 0, 0);
        position: relative;
        overflow: hidden;
    }
    .card:before {
        content: "";
        position: absolute;
        z-index: -1;
        left: 51%;
        right: 51%;
        top: 0;
        background: #427CFF;
        height: 4px;
        -webkit-transition-property: left, right;
        transition-property: left, right;
        -webkit-transition-duration: 0.35s;
        transition-duration: 0.35s;
        -webkit-transition-timing-function: ease-out;
        transition-timing-function: ease-out;
    }
    .card:hover:before{
        left: 0;
        right: 0;
    }
    .card:hover{
        transform: translateY(25px);
    }
}