.home2-page{
    overflow: hidden;
}
.home2-page .banner{
    background-image: url('../../../../assets/Home2/banner.png');
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    position: relative;
    width:100%;
    padding:22.5% 7.5%;
    h1{
        font-size:50px;
        color:white;
        width: 35%;
    }
    .p{
        font-size: 18px;
        color:white;
        width: 35%;
        margin:20px 0 30px;
    }
    .comment-section{
        position: absolute;
        width: 25%;
        bottom:12.5%;
        right: 7.5%;
        box-shadow: rgba(0, 0, 0, 0.15) 2px 2px 4px;
        background-color: white;
        border-radius: 10px;
        padding:30px;
    }
    .comment svg{
        color:#427CFF;
        font-size: 20px;
    }
    .comment p{
        font-size: 17px;
        margin:5px 0 20px;
    }
    .name{
        display: flex;
        align-items: center;
    }
    .block{
        display: flex;
        flex-direction: column;
        flex: 1;
        align-items: flex-end;
        margin-right: 15px;
    }
    .profile{
        width: 60px;
        content: url('../../../../assets/Home2/profile.png');
    }
    span svg{
        font-size: 17px;
        color:#427CFF;
        margin:0 1px;
    }
    .comment-section small{
        font-size: 17px;
        margin-bottom: 5px;
    }
}

@media(max-width:1401px){
    .home2-page .banner{
        padding:17% 7.5%;
        h1{
            font-size: 35px;
            width: 40%;
        }
        .p{
            font-size: 15px;
            width: 40%;
            margin:15px 0 25px;
        }
        .comment-section{
            width: 27.5%;
            bottom:10%;
            right: 7.5%;
            padding:20px;
        }
        .comment svg{
            font-size: 19px;
        }
        .comment p{
            font-size: 14px;
            margin:5px 0 20px;
        }
        .block{
            margin-right: 15px;
        }
        .profile{
            width: 50px;
        }
        span svg{
            font-size: 14px;
            margin:0 1px;
        }
        .comment-section small{
            font-size: 14px;
        }
    }
}

@media(max-width:900px){
    .home2-page .banner{
        padding:15% 7.5%;
        h1{
            width: 50%;
        }
        .p{
            width: 50%;
        }
        .comment-section{
            width: 32.5%;
        }
    }
}

@media(max-width:767px){
    .home2-page .banner{
        padding:120px 7.5% 80px;
        h1{
            font-size: 20px;
            width: 100%;
            text-align: center;
        }
        .p{
            font-size: 14px;
            width: 100%;
            text-align: center;
        }
        a{
            margin:0 auto;
        }
        .comment-section{
            position: relative;
            width: 280px;
            margin:0 auto;
            max-width: 100%;
            bottom:unset;
            right: unset;
            box-shadow: rgba(0, 0, 0, 0.15) 2px 2px 4px;
            margin-top: 30px;
        }
        .comment svg{
            font-size: 16px;
        }
        .comment p{
            font-size: 13px;
            margin:5px 0 15px;
        }
        .block{
            margin-right: 15px;
        }
        .profile{
            width: 45px;
        }
        span svg{
            font-size: 14px;
            margin:0 0.5px;
        }
        .comment-section small{
            font-size: 13px;
        }
    }
}

//================================================
.home2-page .home-items{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin:5% 0;
    padding:0 7.5%;
    .item{
        display: flex;
        border:1px solid #ddd;
        padding:35px;
        width: 45%;
        border-radius: 15px;
        position: relative;
    }
    .icon{
        width: fit-content;
        font-size: 65px;
        color:#427CFF;
        margin-right: 35px;
    }
    .block{
        flex: 1;
    }
    h6{
        font-size: 20px;
    }
    p{
        margin:10px 0 60px;
        font-size: 18px;
    }
    a{
        position: absolute;
        bottom: 20px;
    }
}

@media(max-width:1401px){
    .home2-page .home-items{
        .item{
            padding:20px;
        }
        .icon{
            font-size: 55px;
            margin-right: 30px;
        }
        h6{
            font-size: 16px;
        }
        p{
            margin:10px 0 65px;
            font-size: 15px;
        }
        a{
            bottom: 15px;
        }
    }
}

@media(max-width:767px){
    .home2-page .home-items{
        margin:35px 0;
        padding:0 7.5%;
        .item{
            width: 350px;
            border-radius: 10px;
           margin:10px auto;
        }
        .icon{
            font-size: 45px;
            margin-right: 20px;
        }
        h6{
            font-size: 15px;
        }
        p{
            margin:10px 0 60px;
            font-size: 14px;
        }
    }
}

.home2-page .shared-fingerprint-password{
    margin:0 !important;
}

.home2-page .banner .comment-section{
    &:hover{
        background-image:linear-gradient(rgba(0,0,0,0.75), rgba(0,0,0,0.75)), url('../../../../assets/Home2/item-bg.png');
        background-repeat: no-repeat;
        background-size: cover;
        color:#fff;
        svg{
            color: #fff;
        }
    }
    &:hover{
        transform: scale(1.03);
        transition: 0.3s ease-in-out;
    }
}

.home2-page .home-items{
    .item{
        // display: inline-block;
        // vertical-align: middle;
        -webkit-transform: perspective(1px) translateZ(0);
        transform: perspective(1px) translateZ(0);
        box-shadow: 0 0 1px rgba(0, 0, 0, 0);
        position: relative;
        -webkit-transition-property: color;
        transition-property: color;
        -webkit-transition-duration: 0.3s;
        transition-duration: 0.3s;
    }
    .item:before{
        content: "";
        position: absolute;
        z-index: -1;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: linear-gradient(90deg,rgba(66, 124, 255, 0.4),rgba(66, 124, 255, 0.9));
        -webkit-transform: scaleX(0);
        transform: scaleX(0);
        -webkit-transform-origin: 0 50%;
        transform-origin: 0 50%;
        -webkit-transition-property: transform;
        transition-property: transform;
        -webkit-transition-duration: 0.3s;
        transition-duration: 0.3s;
        -webkit-transition-timing-function: ease-out;
        transition-timing-function: ease-out;
        border-radius: 10px;
      }
    .item:hover{
        color: white;
        border:1px solid transparent;
        svg{
            color:#fff;
        }
    }
    .item:hover:before{
        -webkit-transform: scaleX(1);
        transform: scaleX(1);
    }
}